            />
          </th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="(row, index) in prefix" :key="index">
          <!-- <td>
            <SelectField
              v-model="row.key"
              class="col"
              :options="prefixList"
              :is-clearable="false"
              :is-readonly="row.key === 'seperator' || row.key === 'reset'"
            />
          </td> -->
          <td>
            <template v-if="row.key === 'seperator'">
              <TextField
                v-model="row.key"
                class="col text-capitalize"
                :is-clearable="true"
                :is-readonly="row.key === 'seperator'"
              />
            </template>
            <template v-if="row.key === 'reset'">
              <TextField
                v-model="row.key"
                class="col text-capitalize"
                :is-clearable="true"
                :is-readonly="row.key === 'reset'"
              />
            </template>
            <template v-if="row.key !== 'seperator' && row.key !== 'reset'">
              <ValidationProvider
                v-slot="{ errors }"
                :name="row.key"
                class="col-12"
                :rules="{ required: true }"
              >
                <SelectField
                  v-model="row.key"
