                  :options="prefixList"
                  is-searchable
                  :error="errors[0]"
                  @input="selectedKey"
                />
              </ValidationProvider>
            </template>
          </td>
          <td>
            <div class="row items-center">
              <template v-if="row.key === 'prefix'">
                <ValidationProvider
                  v-slot="{ errors }"
                  :name="row.key"
                  class="col-12"
                  :rules="{ required: true }"
                >
                  <TextField
                    v-model="row.value"
                    class="col"
                    :is-clearable="true"
                    :error="errors[0]"
                    :placeholder="'enter the prefix'"
                  />
                </ValidationProvider>
              </template>
              <template v-if="row.key === 'autoIncrement'">
                <ValidationProvider
                  v-slot="{ errors }"
                  :name="row.key"
                  class="col-12"
                  :rules="validationRules"
                >
                  <CounterField
                    v-model="row.value"
                    class="col"
                    :is-clearable="false"
                    :placeholder="'enter no.of digit'"
                    :error="errors[0]"
                  />
                </ValidationProvider>
              </template>
              <template v-if="row.key === 'year'">
                <ValidationProvider
                  v-slot="{ errors }"
                  :name="row.key"
                  class="col-12"
                  :rules="{ required: true }"
                >
                  <SelectField
                    v-model="row.value"
                    class="col"
                    :options="years"
                    is-searchable
                    :error="errors[0]"
                  />
                </ValidationProvider>
              </template>
              <template v-if="row.key === 'month'">
                <ValidationProvider
                  v-slot="{ errors }"
                  :name="row.key"
                  class="col-12"
