              this.sla.settings.slaMasterFormId = 0;
              this.sla.settings.workflowFormSLAFieldId = "";
              if (this.block.settings.slaRules.length) {
                let slaRules = this.block.settings.slaRules.map((field) => {
                  if (typeof field.settingsJson === "string") {
