<template>
  <FormSection
    :icon="isActive ? 'mdi-poll' : 'mdi-poll-outline'"
    :is-active="isActive"
    v-on="$listeners"
  >
    <template #title>Business Hours</template>
    <template #description> Businesses to define operational hours </template>

    <div id="sla-settings">
      <MultipleChoiceField
        v-model="value.settings.workDays"
        label="Working Days"
        :options="workDays"
        :options-per-line="4"
        class="q-pb-md"
      />

      <FormFieldLabel label="Working Hours" class="action-header" />

      <div class="row q-pb-md">
        <div class="col-4">
          <TimeField
            v-model="value.settings.workHours.from"
            label="Start Time"
            class="q-mr-md"
            is-mandatory
          />
        </div>
        <div class="col-4">
          <TimeField
            v-model="value.settings.workHours.to"
            label="Closing Time"
            class="q-mr-md"
            is-mandatory
          />
        </div>
        <div class="col-12">
          <SelectField
            v-model="value.settings.timeZone"
            label="Work Time Zone"
            class="col q-mt-md"
            :options="timeZones"
            :is-clearable="false"
          />
        </div>
      </div>
    </div>
  </FormSection>
</template>

<script>
import FormSection from "@/components/common/item-builder/FormSection.vue";
import FormFieldLabel from "@/components/common/form/FormFieldLabel.vue";
import MultipleChoiceField from "@/components/common/form/multiple-choice-field/MultipleChoiceField.vue";
import SelectField from "@/components/common/form/select-field/SelectField.vue";
import TimeField from "@/components/common/form/time-field/TimeField.vue";
import { common } from "@/api/factory.js";
export default {
  name: "SLASettings",

  components: {
    FormSection,
    FormFieldLabel,
    MultipleChoiceField,
    SelectField,
    TimeField,
  },

  props: {
    value: {
      type: Object,
      default: () => {},
    },
    isActive: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      workDays: [
        {
          id: this.$nano.id(),
          label: "Sun",
          value: "0",
        },
        {
          id: this.$nano.id(),
          label: "Mon",
          value: "1",
        },
        {
          id: this.$nano.id(),
          label: "Tue",
          value: "2",
        },
        {
          id: this.$nano.id(),
          label: "Wed",
          value: "3",
        },
        {
          id: this.$nano.id(),
          label: "Thu",
          value: "4",
        },
        {
          id: this.$nano.id(),
          label: "Fri",
          value: "5",
        },
        {
          id: this.$nano.id(),
          label: "Sat",
          value: "6",
        },
      ],
      timeZones: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler: function () {
        this.$emit("input", this.value);
      },
    },
  },

  created() {
    this.getTimeZones();
  },

  methods: {
    async getTimeZones() {
      const { error, payload } = await common.getTimeZones();

      if (error) {
        this.$alert.error(error);
        return;
      }
      if (payload) {
        this.timeZones =
          payload &&
          payload.map((row) => ({
            id: this.$nano.id(),
            label: row.displayName,
            value: row.id,
          }));
      }
    },
  },
};
</script>

