  table {
    width: 100%;
    border-collapse: collapse;
    white-space: nowrap;
    margin-bottom: 15px;

    thead {
      border-bottom: 1px solid var(--divider-color);
    }

    tr {
      th {
        text-align: left;
        color: var(--icon-color-inverted);
        font-weight: normal;
        font-size: 0.8125rem;
      }
    }

    td {
      padding-top: 10px;
    }

    .small {
      width: 5%;
    }

    .medium {
      width: 10%;
    }

    .custom-center {
      text-align: center;
    }
  }
}
</style>
<style lang="scss">
#connections {
  .custom-center {
    #checkbox-field .option {
      display: unset !important;
    }
  }

  .field {
    margin: 16px 16px 16px 0px;
  }

  .action-header {
    border-bottom: 1px solid var(--divider-color);
